body {
  background-color: #405470;
  font-weight: 400;
  margin: 0;
  padding: 0;
  height: 100vh;
}
#root {
  background-color: #405470;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
#editorArea, #preview-area {
  background-color: grey;
  display: block;
  justify-content: center;
  align-items: start;
  height: 35%;
  width: 100%;
  margin: 2%;
  padding: 0;
}
#preview-area {
  margin-top: 6em;
  height: 1px;
}
#preview {
  min-height: 15em;
  width: 100%;
  border: 1px solid black;
  border-top: none;
  background-color: #c5ced9;
  box-sizing: border-box;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 1em;
  word-break: break-word;
  resize: none;
}
#editor {
  background-color: #c5ced9;
  height: 100%;
  width: 100%;
  border: 1px solid black;
  border-top: none;
  resize: none;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 1.2em;
  word-break: break-word;
}
.toolbar {
  box-sizing: border-box;
  border: 1px solid black;
  background-color: #374860;
  width: 100%;
  height: 3em;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center; 
  justify-items: center;
}
.toolbarText {
  padding: 0;
  margin: 0;
  font-size: 2em;
}
.toolbar-text-wrapper {
  padding: 0;
  margin: 0em 0em 0em 1em;
  height: 100%;
  display: flex;
  align-items: center; 
}
.toolbar-logo {
  padding: 0;
  margin: 0;
  height: 3em;
  width: fit-content;
}
.App-logo {
  width: 3em;
  height: 3em;
  padding: 0;
  margin: 0;
}
.App {
  background-color: #405470;
  display: block;
  align-items: start;
  height: 100%;
  width: 65%;
  margin: 0;
  padding: 0;
}
#preview blockquote {
  border-left: 3px solid #224b4b;
  color: #224b4b;
  padding-left: 5px;
  margin-left: 25px;
}
#preview > p > code {
  padding: 3px;
}
#preview code {
  background: white;
  font-size: 0.875em;
  font-weight: bold;
}
#preview pre {
  display: block;
  overflow: auto;
  background: white;
  padding: 5px;
  line-height: 1.2;
}
#preview h1 {
  border-bottom: 2px solid #224b4b;
}
#preview h2 {
  border-bottom: 1px solid #224b4b;
}
#preview table {
  border-collapse: collapse;
}
#preview td,
#preview th {
  border: 2px solid #224b4b;
  padding-left: 5px;
  padding-right: 5px;
}
#preview img {
  display: block;
  max-width: 90%;
  margin: 2em auto;
}